import './Reciept.css'
import rmclogo from '../images/rmclogo.png'
import rmclogo2 from '../images/rmclogo2.jpg'
import swachhBharat from '../images/swachhBharat.png'
import React from 'react'
import { indianAmount, nullToNA } from '../../../../../Components/Common/PowerUps/PowerupFunctions'
import QrCode from '../../../../Trade/tradeComponent/QrCode'

class SamReciept extends React.Component {
    render() {

        const samDetails = this.props?.samDetails
        const data = this?.props?.ulbData

        let notes = [
            `कर निर्धारण की सूची, ${data?.ulb_name} Website, ${data?.website} पर प्रदर्शित है।`,

            "नियमावली कंडिका 11.4 के आलोक में वर्षाजल संरक्षण की व्यवस्था नहीं होने के कारण अतिरिक्त गृह कर लगाया जायेगा जो सम्पति कर का 50% होगा । हिदायत दी जाती है कि, वर्षाजल संरक्षण संरचना लगा कर निगम को सूचित करे तथा अतिरिक्त गृह कर से राहत पायें ।",

            "प्रत्येक वित्तीय वर्ष में सम्पति कर का भुगतान त्रैमासिक देय होगा ।",

            "यदि किसी वर्ष के लिए सम्पूर्ण घृति कर का भुगतान वित्तीय वर्ष के 30 जून के पूर्व कर दिया जाता है, तो करदाता को 5% की रियातय दी जाएगी।",

            "किसी देय घृति को निर्दिष्ट समयावधि (प्रत्येक तिमाही) के अन्दर या उसके पूर्व नहीं चुकाया जाता है, तो 1% प्रतिमाह की दर से साधारण ब्याज देय होगा।",

            "यह कर निर्धारण आपके स्व-निर्धारण एवं की गई घोषणा के आधार पर की जा रही है, इस स्व-निर्धारण -सह-घोषणा पत्र की स्थानीय जांच यथा समय निगम करा सकती है एवं तथ् गलत पाए जाने पर नियमावली कंडीका 13.2 के अनुसार निर्धारित शास्ति (Fine) एवं अंतर राशि देय होगा।",

            `${data?.ulb_name} द्वारा संग्रहित इस सम्पति कर इन इमरतों/तोंढांचों को कोई कानूनी हैसियत प्रदान नहीं करता है और/या न ही अपने मालिकों /कों दखलकार को कोई कानूनी अधिकार प्रदान करता है।`,

            "अगर आपके नये होल्डिंग नंओ का आखिरी अंक 5/6/7/8 है तो यह विशिष्ट संरचनाओं की श्रेणी के अन्तर्गत माना जायेगा ।",
        ]

        const noteList = notes.map((content, index) => {
            return <li>
                <span>{index + 1}.&nbsp;</span>
                <span>{content}</span>
            </li>;
        });

        return (
            <>
                <div id="printableArea">

                    <div className="mt-4 flex items-center justify-center text-black">
                        <div className="container w-[70%] border-2 border-dashed m-2 overflow-x-hidden border-black py-4 px-3 relative">

                            <img src={data?.state_logo} alt="" srcset="" className='absolute top-[18%] left-[18%] h-[60vh] backImage opacity-10' />

                            {/* Logo Heading */}
                            {/* Logo Heading */}
                            <div>
                                <div className="flex flex-col justify-center items-center gap-x-4">
                                    <img src={data?.ulb_logo} alt="Logo" srcset="" className="h-16 w-16" />
                                    <span className="text-3xl font-bold uppercase">{data?.ulb_name}</span>
                                </div>
                                <div className='w-full flex justify-center'>
                                    <div className='w-full flex justify-center mt-2'>
                                        <div className='grid grid-cols-6 w-max'>
                                            {/* <div className="col-span-6 uppercase border-2 font-bold border-black px-10 py-2 text-xl">SAM Reciept</div> */}
                                            <div className="col-span-6 uppercase border-2 font-bold border-black px-8 py-1 text-md">SAM Reciept</div>
                                        </div>
                                    </div>
                                </div>
                            </div>

                            <div>
                                <hr className='border-black mt-4 mb-2' />
                                <div className='font-semibold text-xs'>
                                    {/* Information of self-determined property tax under section 152 (3) of the Jharkhand Municipal Act-2011. */}
                                    झारखण्ड नगरपालिका अधिनियम-2011 की धारा 152 (3) के अन्तर्गत स्वनिर्धारित किये गये संम्पती कर की सूचना।
                                </div>
                                <hr className='border-black mb-4 mt-2' />
                            </div>

                            {/* left content */}
                            <div className='flex justify-between font-semibold mb-2 gap-2'>
                                <div className="text-start text-xs font-semibold w-[65%]">
                                    Mr/Mrs/Ms: <span className="font-normal capitalize">{nullToNA(samDetails?.owner_name)} {nullToNA(samDetails?.relation_type)} {nullToNA(samDetails?.guardian_name)}</span> <br />
                                    Address: <span className="font-normal capitalize">{nullToNA(samDetails?.prop_address)}</span>
                                </div>
                                <div className="text-left text-xs w-[35%]">
                                    Memo No.: <span className="font-normal">{nullToNA(samDetails?.memo_no)}</span> <br />
                                    Date: <span className="font-normal">{nullToNA(samDetails?.memo_date)}</span> <br />
                                    Effective: <span className="font-normal">
                                        {samDetails?.from_qtr == 1 && <>First</>}{samDetails?.from_qtr == 2 && <>Second</>}{samDetails?.from_qtr == 3 && <>Third</>}{samDetails?.from_qtr == 4 && <>Fourth</>} Quarter {nullToNA(samDetails?.from_fyear)}</span> <br />
                                </div>

                            </div>

                            {/* Name and Address */}


                            {/* declaration */}
                            <div>
                                <p className="text-xs mb-2">
                                    &nbsp; 
                                    
                                    एतद् द्वारा आपको सूचित किया जाता है कि आपका नया गृह सं॰ - <span className="font-semibold">{(samDetails?.pt_no == '' || samDetails?.pt_no == undefined) ? samDetails?.holding_no : samDetails?.pt_no}</span> वार्ड सं॰ <span className="font-semibold">{nullToNA(samDetails?.ward_no)}</span> हुआ है, आपके स्व॰ निर्धारण घोषणा पत्र के
आधार पर वार्षिक किराया मूल्य <span className="font-semibold">{indianAmount(samDetails?.arv)}/-</span> निर्धारित किया गया है । <br />
इसके अनुसार प्रति तिमाही कर निम्न प्रकार होगा।
                                </p>
                            </div>

                            {/* table */}
                            <div>
                                {/* <div className="text-xs">
                                    Accordingly the tax per quarter will be as follows.
                                </div> */}

                                <table className="text-xs w-full border-2 border-black">

                                    <tr className="font-semibold">
                                        <td colSpan={3} className="text-center">
                                            Self assessment tax notice
                                        </td>
                                    </tr>

                                    <tr className="font-semibold">
                                        <td className="border-2 border-black w-[25%] px-2">SL.No.</td>
                                        <td className="border-2 border-black w-[50%] px-2">Particulars</td>
                                        <td className="border-2 border-black w-[25%] px-2">Amount</td>
                                    </tr>

                                    {
                                        samDetails?.taxTable != undefined && Object.entries(samDetails?.taxTable)?.map(([key, value], index) => <>
                                            {/* samDetails?.taxTable?.forEach((key, value, index) => <> */}
                                            <tr>
                                                <td className="px-2 border-2 border-black">{index + 1}</td>
                                                <td className="px-2 border-2 border-black">{nullToNA(key)}</td>
                                                <td className="px-2 border-2 border-black">{indianAmount(value)}</td>
                                            </tr>
                                        </>)
                                    }

                                    {/* <tr>
                                <td className="px-2 border-2 border-black">1.</td>
                                <td className="px-2 border-2 border-black">House Tax</td>
                                <td className="px-2 border-2 border-black">{indianAmount(samDetails?.holding_tax)}</td>
                            </tr>

                            <tr>
                                <td className="px-2 border-2 border-black">2.</td>
                                <td className="px-2 border-2 border-black">Water Tax</td>
                                <td className="px-2 border-2 border-black">{indianAmount(samDetails?.water_tax)}</td>
                            </tr>

                            <tr>
                                <td className="px-2 border-2 border-black">3.</td>
                                <td className="px-2 border-2 border-black">Latrine Tax</td>
                                <td className="px-2 border-2 border-black">{indianAmount(samDetails?.latrine_tax)}</td>
                            </tr>

                            <tr>
                                <td className="px-2 border-2 border-black">4.</td>
                                <td className="px-2 border-2 border-black">RWH Penalty</td>
                                <td className="px-2 border-2 border-black">{indianAmount(samDetails?.rwh_penalty)}</td>
                            </tr>

                            <tr>
                                <td className="px-2 border-2 border-black">5.</td>
                                <td className="px-2 border-2 border-black">Education Cess</td>
                                <td className="px-2 border-2 border-black">{indianAmount(samDetails?.education_cess)}</td>
                            </tr>

                            <tr>
                                <td className="px-2 border-2 border-black">6.</td>
                                <td className="px-2 border-2 border-black">Health Cess</td>
                                <td className="px-2 border-2 border-black">{indianAmount(samDetails?.health_cess)}</td>
                            </tr> */}

                                    <tr>
                                        <td className="px-2 border-2 border-black font-semibold text-right" colSpan={2}>Total Amount (per quarter)</td>
                                        <td className="px-2 border-2 border-black">{indianAmount(samDetails?.quarterly_tax)} </td>
                                    </tr>

                                </table>
                            </div>

                            {/* qr & signature */}
                            <div className="my-4 w-full flex flex-row justify-between items-start">

                                {/* qr */}
                                <div>
                                    <QrCode size='60' url={window.location.href} />
                                </div>

                                {/* signature */}
                                {/* <div>
                            <div className="h-8 w-52 border-2 border-black"></div>

                            <div className="text-xs font-semibold text-end">
                                To be signed by the Applicant
                            </div>
                        </div> */}

                            </div>

                            {/* note */}
                            <div className="text-xs">
                                <div className="font-semibold my-2">
                                    Note:-
                                </div>

                                <ol className="w-full flex flex-col gap-x-2 pl-10">
                                    {noteList}
                                </ol>

                            </div>

                            {/* bottom note */}
                            <div className="font-semibold mt-4 text-sm">
                                NOTE: This is a computer generated receipt. This receipt does not require physical signature.
                            </div>

                            <div className='flex justify-center items-center mt-2'>
                                <img src={swachhBharat} alt="" className="h-10 opacity-70" />
                            </div>

                        </div>
                    </div>

                </div>

            </>
        )
    }
}

export default SamReciept